import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Spinner } from 'reactstrap'
import {
  Row,
  Col,
  TabContent,
  TabPane,
  Card,
  FormGroup,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Input,
  Form,
  Alert,
  Button,
  CardHeader,
  CardBody,
  CardTitle,
  Label
} from 'reactstrap'
import DataTable from 'react-data-table-component'
import Swiper from 'react-id-swiper'
import { encryptdata, decryptdata } from '../../utility/context/SecurityTool'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Select from 'react-select'
import { toast } from 'react-toastify'
import { Eye, EyeOff, Trash, ChevronDown } from 'react-feather'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import Breadcrumbs from '../../components/@vuexy/breadCrumbs/BreadCrumb'
import classnames from 'classnames'
import AddAssets from './AddAssets'
import GoalOverview from '../ui-elements/cards/analytics/GoalOverview'
import { useDropzone } from 'react-dropzone'
import '../../assets/scss/plugins/extensions/react-paginate.scss'
import '../../assets/scss/pages/data-list.scss'

import themeConfig from '../../configs/themeConfig'

import '../../assets/scss/plugins/extensions/dropzone.scss'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";

const DeleteComponent = (props) => {
  const [open, setOpen] = useState(false)
  return (
    <>
      <Modal isOpen={open} className="modal-dialog-centered" backdrop="static">
        <ModalBody>Are you sure you want to delete this record?</ModalBody>
        <ModalFooter>
          <Button
            color="primary"
            onClick={() => setOpen(false)}
            className="button-label"
          >
            Cancel
          </Button>
          <Button
            color="danger"
            onClick={() => {
              props.deleteRow()
              setOpen(false)
            }}
            className="button-label"
          >
            Delete
          </Button>
        </ModalFooter>
      </Modal>
      <div className="data-list-action">
        <Trash
          className="cursor-pointer"
          size={window.screen.width < 600 ? 13 : 20}
          onClick={() => setOpen(true)}
        />
      </div>
    </>
  )
}

const ViewComponent = (props) => {
  return (
    <div className="data-list-action">
      <Eye
        className="cursor-pointer"
        size={window.screen.width < 600 ? 13 : 20}
        onClick={() => {
          if (!props.showPass) {
            props.set()
          } else {
            let passwordShowTime = JSON.parse(
              localStorage.getItem('logInUserData')
            ).pwdtime
            if (passwordShowTime > 10 || passwordShowTime < 2) {
              passwordShowTime = 3
            }
            toast.warning(
              'Wait for ' +
                passwordShowTime +
                ' seconds as one password is already visible !'
            )
          }
        }}
      />
    </div>
  )
}

const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: 'rgba(115,103,240,.05)',
      color: '#7367F0 !important',
      boxShadow: '0 0 1px 0 #7367F0 !important',
      '&:hover': {
        transform: 'translateY(0px) !important'
      }
    }
  }
}

const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

const colourOptions1 = [
  {
    value: 'Bank',
    label: 'Bank',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Email',
    label: 'Email',
    color: '#0052CC',
    isFixed: true
  },
  {
    value: 'Card',
    label: 'Card',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Social Media',
    label: 'Social Media',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'HealthCare',
    label: 'HealthCare',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'App',
    label: 'App',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Goverment Portal',
    label: 'Goverment Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Office Portal',
    label: 'Office Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Educational Portal',
    label: 'Educational Portal',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'E-commerce',
    label: 'E-commerce',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Finance',
    label: 'Finance',
    color: '#00B8D9',
    isFixed: true
  },
  { value: 'Custom', label: 'Custom', color: '#0052CC', isFixed: true }
]
class PasswordVault extends React.Component {
  state = {
    isLoading: false,
    showPass: false,
    active: '1',
    modal: false,
    pwd: '',
    ptype: '',
    userName: '',
    accountNumber: '',
    description: '',
    confirmPwd: '',
    saved: [],
    selectAssetRef: React.createRef(),
    confirmOpen: false,
    idx: '',
    columns: [
      {
        name: 'Alias',
        selector: 'pwdname',
        sortable: false,
        width: '160px'
      },
      {
        name: 'Type',
        selector: 'ptype',
        sortable: true,
        width: '160px'
      },
      {
        name: 'UserName',
        selector: 'userName',
        sortable: true,
        width: '130px',
        cell: (row) => <span>{row.showPass ? row.userName : '********'}</span>
      },
      {
        name: 'Password',
        selector: 'pwd',
        width: '130px',
        cell: (row) => <span>{row.showPass ? row.pwd : '********'}</span>
      },
      {
        name: 'View/Bin',
        sortable: false,
        cell: (row) => (
          <div
            style={{
              justifyContent: 'space-between',
              display: 'flex',
              width: '100%'
            }}
          >
            <ViewComponent
              showPass={this.state.showPass}
              set={() =>
                this.setState({
                  showIdx: row,
                  modal: !this.state.modal
                })
              }
            />
            <DeleteComponent row={row} deleteRow={() => this.deletepwd(row)} />
          </div>
        ),
        width: '90px'
      }
    ],
    mobilecolumns: [
      {
        name: 'Info',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <ViewComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
            deleteRow={this.handleDelete}
          />
        )
      },
      {
        name: 'Name',
        selector: 'name',
        sortable: true,
        minWidth: '50px',
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.name}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.name}
          </p>
        )
      },
      {
        name: 'Relation',
        selector: 'relation',
        maxWidth: '50px',
        sortable: true,
        cell: (row) => (
          <p
            style={{ fontSize: '10px' }}
            title={row.relation}
            className="text-truncate text-bold-500 mb-0"
          >
            {row.relation}
          </p>
        )
      },
      {
        name: 'Trash',
        sortable: true,
        width: '50px',
        cell: (row) => (
          <DeleteComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
            deleteRow={this.handleDelete}
          />
        )
      }
    ]
  }

  update = () => {
    const logInUserData = JSON.parse(localStorage.getItem('logInUserData'))
    if (!logInUserData) return
    axios
      .post('/backendapi/pwd/list', {
        user: logInUserData._id
      },{headers: {'Authorization':'Bearer '+localStorage.getItem('authtoken')}})
      .then((res) => {
        for (let i = 0; i < res.data.length; i = i + 1) {
          res.data[i].ptype = decryptdata(res.data[i].ptype)
          res.data[i].pwd = decryptdata(res.data[i].pwd)
          res.data[i].pwdId = decryptdata(res.data[i].pwdId)
          res.data[i].pwdname = decryptdata(res.data[i].pwdname)
          res.data[i].userName = decryptdata(res.data[i].userName)
        }
        this.setState({ saved: res.data.reverse() })
      })
  }

  componentWillMount() {
    this.update()
  }

  togglepwdshow = (value, row) => {
    const theLocations = this.state.saved.map((l) => Object.assign({}, l))
    theLocations[row].showPass = value
    this.setState({ saved: theLocations })
    this.setState({ showPass: value })
  }

  toggleModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
      confirmPwd: '',
      showIdx: ''
    }))
  }

  toggleModal2 = () => {
    let passwordShowTime = JSON.parse(
      localStorage.getItem('logInUserData')
    ).pwdtime
    if (passwordShowTime > 10 || passwordShowTime < 2) {
      passwordShowTime = 3
    }

    if (this.state.confirmPwd === '123') {
      const { showIdx, saved } = this.state
      const index = saved.indexOf(showIdx)
      this.togglepwdshow(true, index)
      setTimeout(
        function () {
          this.togglepwdshow(false, index)
          this.setState({ showPass: false })
        }.bind(this),
        passwordShowTime ? passwordShowTime * 1000 : 3000
      )
      this.setState((prevState) => ({
        modal: !prevState.modal,
        confirmPwd: ''
      }))
    } else {
      toast.warning('Wrong Password')
    }
  }

  clearSaved = (e) => {
    this.state.selectAssetRef.current.select.clearValue()
    this.setState({
      pwd: '',
      ptype: '',
      userName: '',
      accountNumber: '',
      description: ''
    })
  }
  addToSaved = (e) => {
    this.setState({ isLoading: true })
    e.preventDefault()
    const { saved, ptype, pwd, userName, accountNumber, description } =
      this.state

    if (ptype === '' || pwd === '' || accountNumber === '') {
      toast.warning('Mandatory Field Missing !!')
      this.setState({ isLoading: false })
    } else {
      const newp = {
        pwdname: accountNumber,
        pwd,
        showPass: false,
        userName: userName,
        accountNumber: accountNumber,
        description: description
      }
      const newp1 = {
        showPass: false,
        pwdname: encryptdata(accountNumber),
        pwd: encryptdata(pwd),
        ptype: encryptdata(ptype),
        userName: encryptdata(userName),
        user: JSON.parse(localStorage.getItem('logInUserData'))._id
      }

      saved.push(newp)
      this.state.selectAssetRef.current.select.clearValue()
      this.setState({
        saved,
        pwd: '',
        ptype: '',
        userName: '',
        accountNumber: '',
        description: ''
      })
      axios.post('/backendapi/savepwd', newp1,{headers: {'Authorization':'Bearer '+localStorage.getItem('authtoken')}}).then((res) => {
        this.update()
        //this.setState({saved:res.data});
        this.setState({ isLoading: false })
        toast.success('Password saved successfully')
      })
    }
  }

  deletepwd = (row) => {
    let { saved } = this.state

    axios.post('/backendapi/pwd/delete', row,{headers: {'Authorization':'Bearer '+localStorage.getItem('authtoken')}}).then((res) => {
      //this.setState({saved:res.data});

      toast.warning('Password deleted successfully')
      saved = saved.filter((ex) => {
        return ex._id !== row._id
      })
      this.setState({ saved })
    })
    this.toggleConfirm(false, null)
  }
  toggleConfirm = (value, id) => {
    this.setState({ confirmOpen: value })
    this.setState({ idx: id })
  }
  setPassword = (e) => {
    const accountTemp = e.target.value.trim()
    accountTemp
      ? this.setState({
          accountNumber: e.target.value
        })
      : this.setState({
          accountNumber: ''
        })
  }
  setUserName = (e) => {
    const userTemp = e.target.value.trim()
    userTemp
      ? this.setState({
          userName: userTemp
        })
      : this.setState({
          userName: ''
        })
  }
  render() {
    console.log('*****', this.state.saved)
    const logInUserData = JSON.parse(localStorage.getItem('logInUserData'))
    let passwordShowTime = ''
    if (logInUserData) {
      passwordShowTime = logInUserData.pwdtime
      if (passwordShowTime > 10 || passwordShowTime < 2) {
        passwordShowTime = 3
      }
    }
    return (
      <React.Fragment>
        <Modal
          isOpen={this.state.modal}
          toggle={this.toggleModal}
          className="modal-dialog-centered"
          fade={false}
        >
          <ModalHeader
            style={{ border: 'solid 1px' }}
            toggle={this.toggleModal}
          >
            Confirm your identity
          </ModalHeader>
          <ModalBody style={{ border: 'solid 1px' }}>
            Enter your login password to continue
            <br />
            <br />
            <FormGroup>
              {/* <Label for="password">Password:</Label> */}
              <Input
                type="password"
                id="password"
                placeholder="Password"
                onChange={(e) => {
                  this.setState({ confirmPwd: e.target.value })
                }}
              />
            </FormGroup>
            <Alert className="mt-1" color="danger">
              <span>
                Your password will only be visible for {passwordShowTime}{' '}
                seconds!
              </span>
            </Alert>
          </ModalBody>
          <ModalFooter>
            <Button
              color={this.state.confirmPwd === '' ? 'primary' : 'success'}
              disabled={this.state.confirmPwd === ''}
              onClick={this.toggleModal2}
            >
              Accept
            </Button>{' '}
          </ModalFooter>
        </Modal>
        <h2 className="warning spacing">Password Vault</h2>
        <Row>
          <Col lg="8" md="6" sm="12">
            <div style={{ width: window.screen.width < 500 ? '100%' : '150%' }}>
              <Row>
                <Col sm="12">
                  <TabContent activeTab={this.state.active}>
                    <TabPane tabId="1">
                      <Row>
                        <ToastContainer />
                        <Col lg="8" md="6" sm="12">
                          <Card>
                            <CardHeader>
                              <CardTitle>Save new Password</CardTitle>
                            </CardHeader>
                            <CardBody>
                              <Form>
                                <Row>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="text"
                                        name="accountNumber"
                                        id="accountNumberMulti"
                                        placeholder="Password For *"
                                        value={this.state.accountNumber}
                                        onChange={this.setPassword}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="accountNumberMulti"
                                      >
                                        Password For *
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="text"
                                        name="userName1"
                                        id="userNameMulti1"
                                        placeholder="User Name"
                                        value={this.state.userName}
                                        onChange={this.setUserName}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="userNameMulti"
                                      >
                                        User Name
                                      </Label>
                                    </FormGroup>

                                    {/* <FormGroup className="form-label-group">
                                      <Input
                                      className='input-label'

                                        type="text"
                                        name="description"
                                        id="descriptionMulti"
                                        placeholder="Note"
                                        value={this.state.description}
                                        onChange={(e) => {
                                          this.setState({
                                            description: e.target.value,
                                          });
                                        }}
                                      />
                                      <Label className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              } for="descriptionMulti">Note</Label>
                                    </FormGroup> */}
                                  </Col>

                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Input
                                        className="input-label"
                                        type="password"
                                        name="name"
                                        id="nameMulti"
                                        placeholder="Password *"
                                        value={this.state.pwd}
                                        onChange={(e) => {
                                          this.setState({
                                            pwd: e.target.value
                                          })
                                        }}
                                      />
                                      <Label
                                        className={
                                          themeConfig.theme === 'dark'
                                            ? 'dark-label'
                                            : 'light-label'
                                        }
                                        for="nameMulti"
                                      >
                                        Password *
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        // defaultValue={colourOptions1[0]}

                                        ref={this.state.selectAssetRef}
                                        name="color"
                                        options={colourOptions1}
                                        isClearable={true}
                                        placeholder="Select Password Type *"
                                        onChange={(e) => {
                                          this.setState({
                                            ptype: e ? e.value : ''
                                          })
                                        }}
                                      />
                                    </FormGroup>
                                    {/* <FormGroup className="form-label-group">
                                      <Input
                                        type="text"
                                        name="description"
                                        id="descriptionMulti"
                                        placeholder="Note"
                                        value={this.state.description}
                                        onChange={(e) => {
                                          this.setState({
                                            description: e.target.value,
                                          });
                                        }}
                                      />
                                      <Label for="descriptionMulti">Note</Label>
                                    </FormGroup> */}
                                  </Col>
                                </Row>
                                <Row>
                                  <Col md="6" sm="12">
                                    {/* <FormGroup className="form-label-group">
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        // defaultValue={colourOptions1[0]}

                                        ref={this.state.selectAssetRef}
                                        name="color"
                                        options={colourOptions1}
                                        isClearable={true}
                                        placeholder="Select Password Type..."
                                        onChange={(e) => {
                                          this.setState({
                                            ptype: e ? e.value : "",
                                          });
                                        }}
                                      />
                                    </FormGroup>*/}
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup
                                      className="form-label-group"
                                      style={{
                                        textAlign: 'right',
                                        marginBottom: '0px'
                                      }}
                                    >
                                      <Button.Ripple
                                        outline
                                        color="secondary"
                                        type="reset"
                                        className="button-label"
                                        onClick={(e) => {
                                          this.clearSaved()
                                        }}
                                      >
                                        Reset
                                      </Button.Ripple>
                                      <Button.Ripple
                                        color="warning"
                                        type="reset"
                                        className="ml-1 button-label"
                                        // disabled={this.state.userName === '' || this.state.pwd === undefined || this.state.pwd === null || this.state.pwd === ''}
                                        onClick={(e) => {
                                          !this.state.isLoading &&
                                            this.addToSaved(e)
                                        }}
                                      >
                                        {this.state.isLoading ? (
                                          <Spinner color="white" size="sm" />
                                        ) : (
                                          'Add'
                                        )}
                                      </Button.Ripple>
                                    </FormGroup>
                                  </Col>
                                </Row>
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                        <Col lg="4" md="6" sm="12">
                          <div className="swipermobile">
                            <Swiper {...params}>
                              <div
                                style={{
                                  backgroundImage: `url(${img1})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img2})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img3})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img4})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                              <div
                                style={{
                                  backgroundImage: `url(${img5})`,
                                  backgroundSize: 'contain'
                                }}
                              ></div>
                            </Swiper>
                          </div>
                        </Col>
                      </Row>

                      <Row>
                        <ToastContainer />
                        <Col md="8">
                          <Card>
                            <CardHeader>
                              <CardTitle>Saved Passwords</CardTitle>
                            </CardHeader>
                            <CardBody style={{ paddingTop: '0px' }}>
                              <Form>
                                {this.state.saved.length === 0 && (
                                  <h3 style={{ textAlign: 'center' }}>
                                    No records found!!!
                                  </h3>
                                )}
                                {this.state.saved.length > 0 && (
                                  <div
                                    className={`data-list ${
                                      this.props.thumbView
                                        ? 'thumb-view'
                                        : 'list-view'
                                    }`}
                                  >
                                    <DataTable
                                      width="100"
                                      data={this.state.saved}
                                      columns={this.state.columns}
                                      noHeader
                                      selectableRows={
                                        window.screen.width < 600
                                          ? false
                                          : false
                                      }
                                      responsive
                                      pointerOnHover
                                      selectableRowsHighlight
                                      onSelectedRowsChange={(data) =>
                                        this.setState({
                                          selected: data.selectedRows
                                        })
                                      }
                                      customStyles={selectedStyle}
                                      sortIcon={<ChevronDown />}
                                    />
                                  </div>
                                )}
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>
                    </TabPane>
                    <TabPane tabId="2"></TabPane>
                    <TabPane tabId="3">Coming soon .......!</TabPane>
                  </TabContent>
                </Col>
              </Row>
            </div>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}
export default PasswordVault
