import React, { useRef } from 'react'
import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label
} from 'reactstrap'
import axios from 'axios'
import { connect } from 'react-redux'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import { getData } from '../../redux/actions/data-list/'
import { HelpCircle } from 'react-feather'
import GoalOverview from '../ui-elements/cards/analytics/GoalOverview'
import Select from 'react-select'
import Checkbox from '../../components/@vuexy/checkbox/CheckboxesVuexy'
import { Check, Delete } from 'react-feather'
import Axios from 'axios'

let $primary = '#7367F0',
  $success = '#28C76F',
  $danger = '#EA5455',
  $warning = '#FF9F43',
  $primary_light = '#9c8cfc',
  $warning_light = '#FFC085',
  $danger_light = '#f29292',
  $stroke_color = '#b9c3cd',
  $label_color = '#e7eef7'

const colourOptions = [
  { value: 'Aaquib', label: 'Aaquib', color: '#00B8D9', isFixed: true },
  { value: 'Vipin', label: 'Vipin', color: '#0052CC', isFixed: true },
  { value: 'Aqueel', label: 'Aqueel', color: '#5243AA', isFixed: true },
  { value: 'Vatsal', label: 'Vatsal', color: '#FF5630', isFixed: false },
  { value: 'Pritam', label: 'Pritam', color: '#FF8B00', isFixed: false }
]
const colourOptions1 = [
  {
    value: 'Bank Details',
    label: 'Bank Details',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Fixed Deposit',
    label: 'Fixed Deposit',
    color: '#0052CC',
    isFixed: true
  },
  { value: 'Notes', label: 'Notes', color: '#0052CC', isFixed: true },
  { value: 'Custom', label: 'Custom', color: '#0052CC', isFixed: true }
]

const colourOptions2 = [
  { value: 'Text', label: 'Text', color: '#00B8D9', isFixed: true },
  { value: 'textarea', label: 'Text Area', color: '#0052CC', isFixed: true },
  { value: 'Date', label: 'Date', color: '#0052CC', isFixed: true },
  { value: 'Number', label: 'Number', color: '#0052CC', isFixed: true },
  { value: 'File', label: 'File', color: '#0052CC', isFixed: true }
]

class AddAssets extends React.Component {
  state = {
    assetType: '',
    selectAssetRef: React.createRef(),
    selectNomineeRef: React.createRef(),
    selectCustomRef: React.createRef(),
    nomineeOption: [],
    nominees: [],
    nomineeList: [],
    selectedTemplate: [],
    template: {
      Custom: [
        {
          key: 'Asset Name',
          val: '',
          type: 'Text'
        }
      ],
      Notes: [
        {
          key: 'Notes',
          val: '',
          type: 'textarea'
        },
        {
          key: 'Notes End Date',
          val: '',
          type: 'date'
        }
      ],
      'Fixed Deposit': [
        {
          key: 'Asset Name / Alias',
          val: '',
          type: 'Text'
        },
        {
          key: 'Bank Accrount No',
          val: '',
          type: 'Number'
        },
        {
          key: 'IFSC',
          val: '',
          type: 'text'
        },
        {
          key: 'Amount',
          val: '',
          type: 'Number'
        },
        {
          key: 'FD Nominee Registered at Bank',
          val: '',
          type: 'text'
        },
        {
          key: 'FD Maturity Date',
          val: '',
          type: 'Date'
        },
        {
          key: 'Attachments',
          val: '',
          type: 'File'
        }
      ]
    }
  }

  static getDerivedStateFromProps(props, state) {
    if (props.dataList.data.length !== state.nomineeList.length) {
      const nomineeOption = state.nomineeOption
      props.dataList.data.forEach((e) => {
        const temp = {
          value: e._id,
          label: e.name + ' - ' + e.relation,
          color: '#00B8D9',
          isFixed: false
        }
        nomineeOption.push(temp)
      })

      return {
        nomineeList: props.dataList.data,
        nomineeOption: nomineeOption
      }
    }

    // Return null if the state hasn't changed
    return null
  }
  componentDidMount() {
    this.props.getData(this.props.parsedFilter)
    // this.props.getInitialData()
  }

  changeValue = (e, k) => {
    const selectedTemplate = this.state.selectedTemplate
    const idx = selectedTemplate.findIndex((x) => x.key === k.key)
    console.log('ZZZZZZZZZZZ', e, idx)
    selectedTemplate[idx].val = e
    this.setState({ selectedTemplate })
  }

  callAddAsset = (e) => {
    e.preventDefault()
    console.log(this.state.selectedTemplate)
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    console.log('sssssssssssssssss', this.state.nominee)
    const as = {
      assetType: this.state.assetType,
      assetDetails: this.state.selectedTemplate,
      nominees: this.state.nominees
    }
    user.assets.push(as)
    localStorage.setItem('logInUserData', JSON.stringify(user))
    toast.success('Asset addedd successfully!')
    this.clearAsset()
    this.clearCustomField()
    axios
      .post('/backendapi/adddetails', { _id: user._id, assets: user.assets })
      .then((res) => {})
      .catch()
  }
  addfield = () => {
    const selectedTemplate = this.state.selectedTemplate
    selectedTemplate.push({
      key: this.state.fname,
      val: '',
      type: this.state.ftype,
      deletable: true
    })
    console.log('selectedTemplate', selectedTemplate)
    this.setState({ selectedTemplate, fname: '', ftype: '' })
  }
  changeNominee = (event) => {
    const nominees = this.state.nominees
    event.forEach((e) => {
      nominees.push({ id: e.value, name: e.label })
      console.log('ssssssssssssssssssssss2', e)
    })
    this.setState({ nominees })

    // console.log(event.target);
    // console.log(event.target.value);
  }

  clearAsset = () => {
    this.state.selectAssetRef.current.select.clearValue()
    this.state.selectNomineeRef.current.select.clearValue()
    this.setState({ assetType: '', selectedTemplate: [], nominees: [] })
  }
  clearCustomField = () => {
    this.setState({ fname: '', ftype: '' })
    this.state.selectCustomRef.current.select.clearValue()
  }

  render() {
    return (
      <Row>
        <ToastContainer />
        <Col lg="8" md="6" sm="12">
          <Card>
            <CardHeader>
              <CardTitle>Add New Asset</CardTitle>
            </CardHeader>
            <CardBody>
              <Form className="mt-2">
                <Row>
                  <Col md="6" sm="12">
                    <FormGroup className="form-label-group">
                      <Select
                        className="React"
                        classNamePrefix="select"
                        //defaultValue={colourOptions1[0]}

                        ref={this.state.selectAssetRef}
                        name="color"
                        options={colourOptions1}
                        isClearable={true}
                        placeholder="Select Investment Type..."
                        onChange={(e) => {
                          {
                            this.setState({
                              fname: '',
                              ftype: '',
                              assetType: e ? e.value : undefined,
                              selectedTemplate: e
                                ? this.state.template[e.value]
                                : {}
                            })
                          }
                        }}
                      />
                      <Label for="nameMulti">Asset Type</Label>
                    </FormGroup>
                  </Col>
                  <Col md="6" sm="12">
                    <FormGroup className="form-label-group">
                      <Select
                        //defaultValue={[colourOptions[2], colourOptions[3]]}
                        isMulti
                        name="nominee"
                        options={this.state.nomineeOption}
                        ref={this.state.selectNomineeRef}
                        className="React"
                        classNamePrefix="select"
                        placeholder="Select Nominees..."
                        id="nominee"
                        onChange={this.changeNominee}
                        //onChange={(e) => {{this.setState({nominee: e ? e.value : undefined})}} }
                      />
                      <Label>Nominee</Label>
                    </FormGroup>
                  </Col>
                  {/* {this.state.assetType  && 
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="name"
                    id="nameMulti"
                    placeholder="First Name"
                  />
                  <Label for="nameMulti">First Name</Label>
                </FormGroup>
              </Col>}
              {this.state.assetType  &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="lastname"
                    id="lastNameMulti"
                    placeholder="Last Name"
                  />
                  <Label for="lastNameMulti">Last Name</Label>
                </FormGroup>
              </Col>}
              {this.state.assetType === "Fixed Deposit"  &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="city"
                    id="cityMulti"
                    placeholder="Fixed Deposit Account Number"
                  />
                  <Label for="cityMulti">Fixed Deposit Account Number</Label>
                </FormGroup>
              </Col>} */}
                  {Object.keys(this.state.template).includes(
                    this.state.assetType
                  ) &&
                    this.state.template[this.state.assetType].map((e) => {
                      return (
                        <Col md="6" sm="12">
                          <FormGroup className="form-label-group">
                            {e.deletable && (
                              <Delete
                                style={{
                                  zIndex: 999,
                                  float: 'right',
                                  marginBottom: '-8px',
                                  marginRight: '-5px'
                                }}
                                className="primary"
                                size={15}
                              />
                            )}
                            <Input
                              type={e.type}
                              name="city"
                              id="cityMulti"
                              placeholder={e.key}
                              onChange={(ev) => {
                                this.changeValue(
                                  ev.target ? ev.target.value : ev.value,
                                  e
                                )
                              }}
                            />
                            <Label for="cityMulti">{e.key}</Label>
                          </FormGroup>
                        </Col>
                      )
                    })}
                  {/* {this.state.assetType === "Fixed Deposit" &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="country"
                    id="CountryMulti"
                    placeholder="Bank Name"
                  />
                  <Label for="CountryMulti">Bank Name</Label>
                </FormGroup>
              </Col>}
              {this.state.assetType === "Bank Details"  &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="city"
                    id="cityMulti"
                    placeholder="Bank Account Number"
                  />
                  <Label for="cityMulti">Bank Account Number</Label>
                </FormGroup>
              </Col>}
              {this.state.assetType === "Bank Details" &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="country"
                    id="CountryMulti"
                    placeholder="Bank Name"
                  />
                  <Label for="CountryMulti">Bank Name</Label>
                </FormGroup>
              </Col>}
              {this.state.assetType === "Property" &&
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="country"
                    id="CountryMulti"
                    placeholder="Property Location"
                  />
                  <Label for="CountryMulti">Property Location</Label>
                </FormGroup>
              </Col>} */}
                  {/* <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="text"
                    name="company"
                    id="CompanyMulti"
                    placeholder="Company"
                  />
                  <Label for="CompanyMulti">Company</Label>
                </FormGroup>
              </Col>
              <Col md="6" sm="12">
                <FormGroup className="form-label-group">
                  <Input
                    type="email"
                    name="Email"
                    id="EmailMulti"
                    placeholder="Email"
                  />
                  <Label for="EmailMulti">Email</Label>
                </FormGroup>
              </Col> */}
                  {/* <Col sm="12">
                <FormGroup className="form-label-group">
                  <Checkbox
                    color="primary"
                    icon={<Check className="vx-icon" size={16} />}
                    label="Remember Me"
                    defaultChecked={false}
                  />
                </FormGroup>
              </Col> */}
                  <Col sm="12">
                    <FormGroup className="form-label-group">
                      <Button.Ripple
                        color="primary"
                        type="submit"
                        className="mr-1 mb-1"
                        disabled={this.state.nominees.length === 0}
                        onClick={(e) => {
                          this.callAddAsset(e)
                        }}
                      >
                        Submit
                      </Button.Ripple>
                      <Button.Ripple
                        outline
                        color="warning"
                        type="reset"
                        className="mb-1"
                        onClick={(e) => {
                          this.clearAsset()
                        }}
                      >
                        Reset
                      </Button.Ripple>
                    </FormGroup>
                  </Col>
                </Row>
              </Form>
            </CardBody>
          </Card>
        </Col>
        <Col lg="4" md="6" sm="12">
          <Card>
            <CardHeader>
              <CardTitle>Add Custum Field</CardTitle>
              <HelpCircle size={20} className="cursor-pointer text-muted" />
            </CardHeader>
            <CardBody>
              <Row>
                <Col sm="12">
                  <FormGroup className="form-label-group">
                    <Input
                      type="email"
                      name="Email"
                      disabled={this.state.assetType === ''}
                      id="EmailMulti"
                      value={this.state.fname}
                      placeholder="Enter Field Name"
                      onChange={(e) => {
                        this.setState({ fname: e.target.value })
                      }}
                    />
                    <Label for="EmailMulti">Enter Field Name</Label>
                  </FormGroup>
                </Col>
                <Col sm="12">
                  <FormGroup
                    className="form-label-group"
                    disabled={this.state.assetType === undefined}
                  >
                    <Select
                      className="React"
                      ref={this.state.selectCustomRef}
                      disabled={this.state.assetType === undefined}
                      classNamePrefix="select"
                      isClearable={true}
                      //defaultValue={colourOptions1[0]}
                      name="color"
                      options={colourOptions2}
                      placeholder="Select Field Type..."
                      onChange={(e) =>
                        this.setState({ ftype: e ? e.value : ' ' })
                      }
                    />
                    <Label for="nameMulti">Field Type</Label>
                  </FormGroup>
                </Col>
                <Col md="6" sm="12">
                  <FormGroup className="form-label-group">
                    <Button.Ripple
                      color="primary"
                      disabled={this.state.assetType === undefined}
                      type="submit"
                      className="mr-1 mb-1"
                      onClick={(e) => this.addfield()}
                    >
                      {'<- Add'}
                    </Button.Ripple>
                  </FormGroup>
                </Col>
                <Col md="6" sm="12">
                  <FormGroup className="form-label-group">
                    <Button.Ripple
                      outline
                      color="warning"
                      type="reset"
                      className="mb-1"
                      disabled={this.state.assetType === undefined}
                      onClick={(e) => {
                        this.clearCustomField()
                      }}
                    >
                      Reset
                    </Button.Ripple>
                  </FormGroup>
                </Col>
              </Row>

              {/* <Chart
            options={this.state.options}
            series={this.state.series}
            type="radialBar"
            height={260}
          /> */}
            </CardBody>
            {/* <div className="d-flex mt-2">
          <div className="completed border-top border-right text-center w-50 py-1">
            <p className="mb-50">Completed</p>
            <p className="font-large-1 text-bold-600 mb-50">786,617</p>
          </div>
          <div className="in-progress border-top border-right text-center w-50 py-1">
            <p className="mb-50">In Progress</p>
            <p className="font-large-1 text-bold-600 mb-50">13,561</p>
          </div>
        </div> */}
          </Card>
        </Col>
      </Row>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    dataList: state.dataList
  }
}

export default connect(mapStateToProps, { getData })(AddAssets)

//export default AddAssets
